body {
  height: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  margin: 0;
  padding: 0;
  color: #1d1613;
  background-color: #ffffff;
}
.page.menu-open, .page.popup-open {
  overflow: hidden;
}

.wrapper {
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.page-main {
  flex: 1 0 auto;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-header__upper-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 41px 98px 50px;
  border-bottom: 1px solid #f6f5f4;
}
.page-header__upper-row > a {
  position: absolute;
  left: 50%;
  top: 30px;
  transform: translate(-50%, 0);
}
.page-header__upper-row > a svg {
  fill: #a87b62;
}
.page-header__upper-row > a:hover {
  opacity: 0.7;
}
@media (max-width: 1023px) {
  .page-header__upper-row {
    padding: 41px 98px 45px;
    border-bottom: none;
  }
}
@media (max-width: 767px) {
  .page-header__upper-row {
    padding: 41px 98px 11px;
  }
  .page-header__upper-row > a {
    top: 20px;
  }
  .page-header__upper-row > a svg {
    width: 52px;
    height: 52px;
  }
}
.page-header__search svg {
  fill: #1d1613;
  margin: 0 15px 0 0;
}
.page-header__search a {
  color: #9b9997;
  font-size: 14px;
}
.page-header__search a:hover {
  opacity: 0.7;
}
@media (max-width: 1023px) {
  .page-header__search {
    display: none;
  }
}
.page-header__login a {
  color: #1d1613;
  font-size: 14px;
}
.page-header__login a:first-child {
  margin: 0 35px 0 0;
}
.page-header__login a:hover {
  opacity: 0.7;
}
@media (max-width: 1023px) {
  .page-header__login {
    display: none;
  }
}
.page-header__bottom-row {
  padding: 20px 0;
}

@media (max-width: 1023px) {
  .page-header.no-js {
    background-color: #a87b62;
  }
  .page-header.no-js .page-header__upper-row > a svg {
    fill: #ffffff;
  }

  .page-header.active {
    position: absolute;
    z-index: 2;
    height: 100vh;
    width: 100vw;
    overflow-y: scroll;
    background-color: #a87b62;
  }
  .page-header.active .page-header__upper-row > a svg {
    fill: #ffffff;
  }
}
