.filter {
  display: flex;
  flex-direction: column;
}
.filter > a {
  display: none;
}
.filter__wrapper {
  border: 1px solid #a87b62;
  margin: 0 0 36px 0;
}
.filter__filter {
  padding: 23px 23px 14px 25px;
}
.filter__filter ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: none;
}
.filter__filter ul li {
  margin: 0 0 10px 0;
}
.filter__filter ul label {
  position: relative;
  display: inline-flex;
  align-items: center;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  padding: 0 0 0 35px;
  cursor: pointer;
}
.filter__filter ul label:hover {
  opacity: 0.7;
}
.filter__filter ul input {
  position: absolute;
  opacity: 0;
  z-index: -1;
}
.filter__filter ul input:checked {
  opacity: 0;
}
.filter__filter ul input + label::before {
  position: absolute;
  content: "";
  top: 1px;
  left: 9px;
  width: 15px;
  height: 15px;
  border: 1px solid #a87b62;
}
.filter__filter ul input:focus + label::before {
  border: 1px solid red;
}
.filter__filter ul input:checked + label::after {
  position: absolute;
  content: "";
  top: 1px;
  left: 9px;
  width: 15px;
  height: 15px;
  background-color: #a87b62;
}
.filter__filter ul input:checked:hover + label::after {
  box-shadow: 0 0 16px #a87b62;
}
.filter__filter ul input:checked:focus + label::after {
  box-shadow: 0 0 16px red;
}
.filter__filter.active ul {
  display: block;
  margin: 21px 0 0 0;
}
.filter__filter.active .controls {
  display: block;
}
.filter__filter.active .filter__tumbler svg {
  transform: rotate(180deg);
}
.filter__tumbler a {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}
.filter__tumbler a:hover {
  opacity: 0.7;
}
.filter__tumbler a svg {
  stroke: #a87b62;
  margin: 5px 0 0 0;
}
